body{margin:0;font-family:-apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen', 'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue', sans-serif;-webkit-font-smoothing:antialiased;-moz-osx-font-smoothing:grayscale;overflow:hidden}code{font-family:source-code-pro, Menlo, Monaco, Consolas, 'Courier New', monospace}

.App{text-align:center;overflow:hidden}.App .App-content{width:100vw;min-height:94vh;display:flex;flex-direction:row}

.Navbar{width:100%;min-height:50px;padding:0 2rem;background:#abd8cb;text-align:left;display:flex;flex-direction:row;align-items:center;box-shadow:0 4px 2px -2px gray;position:relative;z-index:100000}.Navbar .title{font-size:1.75rem;margin-left:1rem}

.ChatList{background-color:#f6f6f6;max-height:100vh;overflow:auto;flex:1 1;align-self:stretch}.ChatList ul{list-style-type:none;padding:0;margin:0}.ChatList ul li{border-bottom:1px solid #ddd;padding:1rem 2rem;text-align:left;display:flex;flex-direction:row;align-items:center;cursor:pointer}.ChatList ul li:hover,.ChatList ul li.selected{background-color:#c9dcd7}.ChatList ul li .circular-container{display:inline-block;position:relative;width:60px;height:60px;overflow:hidden;border-radius:50%;border:1px solid #ccc}.ChatList ul li .circular-container img{width:auto;height:100%}.ChatList ul li .username{margin-left:1rem;font-weight:bold}

.ChatWindow{max-height:100vh;overflow-y:auto;overflow-x:hidden;position:relative;flex:3 1;align-self:stretch}.ChatWindow .header{padding:2rem 2rem 1rem;text-align:left;font-weight:bold}.ChatWindow .header .dot{height:10px;width:10px;background-color:#bbb;border-radius:50%;display:inline-block}.ChatWindow .header .dot.online{background-color:green}.ChatWindow .header .username{margin-left:1rem}.ChatWindow ul{list-style-type:none;padding:0 1rem;margin:0}.ChatWindow ul li{display:block;text-align:right}.ChatWindow ul li .message{display:inline-block;max-width:50%;background-color:#c9dcd7;padding:1rem;border-radius:10%;word-wrap:break-word}.ChatWindow ul li.left{text-align:left}.ChatWindow ul li.left .message{background-color:#eee}.ChatWindow .message-input{position:absolute;bottom:0;border-top:1px solid #ccc;padding:1rem;width:100%;box-sizing:border-box}.ChatWindow .message-input textarea{width:70vw;border:none;outline:none;resize:none;font-family:unset;font-size:1rem}.ChatWindow .message-input .hint{text-align:right;font-weight:bold;font-size:0.75rem}

